const check={
	telphone(data){
		if(!(/^1[3456789]\d{9}$/.test(data))){
			uni.showToast({
				title:"",
				icon:"none"
			})
		    return false; 
		}
		return true;
	},
	password(data){
		if(data.length<6){
			uni.showToast({
				title:"",
				icon:"none"
			})
			return false; 
		}
		return true;
	},
	code(data){
		if(data.length!=4){
			uni.showToast({
				title:"",
				icon:"none"
			})
			return false; 
		}
		return true;
	},
	username(data){
		if(data==''){
			uni.showToast({
				title:"",
				icon:"none"
			})
			return false; 
		}
		return true;
	},
	city(data){
		if(data==''||data==""){
			uni.showToast({
				title:"",
				icon:"none"
			})
			return false; 
		}
		return true;
	},
	address(data){
		if(data==''){
			uni.showToast({
				title:"",
				icon:"none"
			})
			return false; 
		}
		return true;
	}
}
export default check;